@import url(https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap);
header{
    background-color: var(--color-red);
    width: 100%;
    color: #FFF;
    text-align: center;
    display: grid;
    align-content: center;
    height: 3rem;
    text-transform: uppercase;
    position: fixed;
    top:0;
    left: 0;
}

h1{
    font-size: 1rem;
    font-weight: normal;
}
.cometa{
    background-color: #FFF;
    text-align: center;
    color: var(--color-blue);
    padding: 1.5rem;
    border-radius: .5rem;
}

.cometa.hazardous{
    border: 4px solid var(--color-red);
}

.cometa .cometa-circulo{
    background-color: var(--color-red);
    height: 1.5rem;
    width: 1.5rem;
    margin: auto;
    border-radius: 50%;
    margin-bottom: 1rem;
}

.cometa h2{
    font-size: 1rem;
    margin-bottom: 1rem;
}

.cometa .cometa-info{
    margin-bottom: 1rem;
}

.cometa .cometa-info:last-of-type{
    margin-bottom: 0;
}

.cometa .cometa-info h3{
    font-size: 1rem;
    color: var(--color-red);
}

main{
    padding: 4rem 1rem 1rem 1rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
}

main .info{
    grid-column: span 3;
    color: #FFF;
    text-align: center;
}

@media screen and (max-width: 769px) {
    main a{
        grid-column: span 3;
    }
}
.detalhe{
    grid-column: span 3;
}

main#cometa a{
    grid-column: span 3;
}

.btn-home{
    color: var(--color-red);
    background-color: #FFF;
    text-align: center;
    border-radius: .5rem;
    padding: .5rem;
    font-weight: bold;
}


.cometa-hazardous{
    text-transform: uppercase;
    color: var(--color-red);
    font-weight: bold;
}

*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

:root{
    --color-red: #FC3D21;
    --color-blue: #0B3D91;
    font-size: 1.5vw;
    line-height: 1.1em;
}

html, body{
    width: 100%;
    height: 100%;
    background-color: #0B3D91;
    background-color: var(--color-blue);
    font-family: 'Karla', sans-serif;;
}

a{
    text-decoration: none;
    
}


@media screen and (max-width: 769px) {
    :root{
        font-size: 2.1vw;
        line-height: 1.1em;
    }
}

@media screen and (max-width: 577px) {
    :root{
        font-size: 4vw;
        line-height: 1.1em;
    }
}
